'use client';

import { useState, useEffect } from 'react';
import TaskList from '../../components/TaskList';
import TaskForm from '../../components/TaskForm';
import { TaskResponse } from '../../../backend/src/models/task';

export default function Dashboard() {
  const [userId, setUserId] = useState<number>(1); // In a real app, this would come from auth context
  const [tasks, setTasks] = useState<TaskResponse[]>([]);
  const [refreshTrigger, setRefreshTrigger] = useState(0);

  const handleTaskCreated = () => {
    // Trigger a refresh of the task list
    setRefreshTrigger(prev => prev + 1);
  };

  const handleTaskUpdated = () => {
    // Trigger a refresh of the task list
    setRefreshTrigger(prev => prev + 1);
  };

  const handleTaskDeleted = () => {
    // Trigger a refresh of the task list
    setRefreshTrigger(prev => prev + 1);
  };

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="container mx-auto px-4 max-w-6xl">
        <h1 className="text-3xl font-bold text-gray-800 mb-8 text-center">Todo Dashboard</h1>
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          <div className="lg:order-2">
            <TaskForm userId={userId} onTaskCreated={handleTaskCreated} />
          </div>
          <div className="lg:order-1">
            <TaskList userId={userId} />
          </div>
        </div>
      </div>
    </div>
  );
}